<template>
  <div>
    <div></div>
    <el-table
      :data="tableData"
      row-key="id"
      lazy
      :load="load"
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="date" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
    </el-table>
    
  </div>
</template>

<script>
import Sortable from "sortablejs";
export default {
  name: "personalList",
  data() {
    return {
      tableData: [
        {
          id: Math.random(),
          date: "2016-05-02",
          name: "",
          address: " 1518 ",
          children: [
            {
              id: Math.random(),
              date: "2016-05-02",
              name: "",
              address: " 1518 ",
            },
          ],
        },
        {
          id: Math.random(),
          date: "2016-05-04",
          name: "",
          address: " 1517 ",
          hasChildren: true,
        },
        {
          id: Math.random(),
          date: "2016-05-01",
          name: "",
          address: " 1519 ",
          hasChildren: true,
        },
        {
          id: Math.random(),
          date: "2016-05-03",
          name: "",
          address: " 1516 ",
        },
      ],
    };
  },

  methods: {
    
    load(tree, treeNode, resolve) {
      console.log(tree, treeNode, resolve);
      setTimeout(() => {
        resolve([
          {
            id: Math.random(),
            date: "2016-05-01",
            name: "",
            address: " 1519 ",
            hasChildren: true,
          },
          {
            id: Math.random(),
            date: "2016-05-01",
            name: "",
            address: " 1519 ",
            children: [
              {
                id: Math.random(),
                date: "2016-05-01",
                name: "",
                address: " 1519 ",
              },
            ],
          },
        ]);
      }, 1000);
    },
  },
};
</script>

<style>
</style>


