<template>
  <div class="block">
    <span class="demonstration">Tag</span>
    <el-cascader
      v-model="ads"
      :options="options"
      :props="props"
      collapse-tags
      clearable
    ></el-cascader>
  </div>
</template>


<script>
export default {
  data() {
    return {
      ads: [],
      props: { multiple: true },
      options: [
        {
          value: 1,
          label: "",
          children: [
            {
              value: 2,
              label: "",
              children: [
                { value: 3, label: "" },
                { value: 4, label: "" },
                { value: 5, label: "" },
              ],
            },
            {
              value: 7,
              label: "",
              children: [
                { value: 8, label: "" },
                { value: 9, label: "" },
                { value: 10, label: "" },
              ],
            },
            {
              value: 12,
              label: "",
              children: [
                { value: 13, label: "" },
                { value: 14, label: "" },
                { value: 15, label: "" },
              ],
            },
          ],
        },
        {
          value: 17,
          label: "",
          children: [
            {
              value: 18,
              label: "",
              children: [
                { value: 19, label: "" },
                { value: 20, label: "" },
              ],
            },
            {
              value: 21,
              label: "",
              children: [
                { value: 22, label: "" },
                { value: 23, label: "" },
              ],
            },
          ],
        },
      ],
    };
  },
};
</script>


